import DeleteOutline from "@mui/icons-material/DeleteOutline";
import Edit from "@mui/icons-material/EditOutlined";
import {
    Box, ListItemButton, ListItemText, Chip, styled, Typography
} from "@mui/material";
import sortBy from "lodash/sortBy";
import flatten from "lodash/flatten";
import { useEffect, useState } from "react";
import { useLocation, useNavigate, useSearchParams } from "react-router-dom";
import { useApplication, useTask } from "../../../../Application";
import { useAuth } from "../../../../auth";
import ResponsiveTable from "../../../../components/ResponsiveTable";
import Text from "../../../../components/Text";
import useAfterFirstRender from "../../../../utils/useAfterFirstRender";
import useGetRowNumber from "../../../../utils/useGetRowNumber";
import useNavigateTask from "../../../../utils/useNavigateTask";

const ClamppedText = styled(Typography)(({ theme }) => ({
    overflow: 'hidden',
    display: '-webkit-box',
    '-webkit-box-orient': 'vertical',
    '-webkit-line-clamp': '2',
}));

const DataTable = ({
    loading,
    data,
    meta,
    infiniteScrollProps,
    onClickEdit,
    onClickDelete,
}) => {

    const { application } = useApplication();
    const { inject } = useAuth();

    const navigate = useNavigate();
    const location = useLocation();

    const [searchParams, setSearchParams] = useSearchParams();

    const [sort, setSort] = useState(
        searchParams.has('sort') ? searchParams.get('sort') : '-createdAt'
    );

    useAfterFirstRender(() => {
        setSearchParams({
            ...Object.fromEntries(searchParams),
            sort
        })
    }, [sort]);

    const getRowNumber = useGetRowNumber();

    const navigateTask = useNavigateTask();
    const taskDetails = useTask('People.details');

    useEffect(() => {
        console.log('emitting...');
        application.socket.emit('customersClub:test');
    }, []);


    return (
        <>
            <ResponsiveTable
                data={data}
                loading={loading}
                infiniteScrollProps={infiniteScrollProps}
                definitions={sortBy([
                    {
                        title: 'ردیف',
                        headerCellProps: {
                            align: "center"
                        },
                        rowCellProps: {
                            align: "center",
                            width: 100
                        },
                        renderCell: (row, index) => <Text>{getRowNumber(index)}</Text>,
                        priority: 100
                    }, {
                        field: 'name',
                        title: 'عنوان',
                        headerCellProps: {
                            align: "center"
                        },
                        rowCellProps: {
                            align: "center",
                            width: 200
                        },
                        // render: row => <Text>{row.code}</Text> || '-',
                        priority: 200
                    }, {
                        field: 'content',
                        title: 'متن پیام',
                        headerCellProps: {
                            align: "center"
                        },
                        rowCellProps: {
                            align: "center",
                            width: 200
                        },
                        render: row => <ClamppedText whiteSpace="pre"><Text>{row.content}</Text></ClamppedText> || '-',
                        priority: 200
                    }, {
                        title: 'دسته‌های هدف',
                        rowCellProps: {
                            width: 300
                        },
                        renderCell: row => (
                            <Text>{row.targets.map(r => r.target.name).join(', ')}</Text>
                        ),
                        priority: 300
                    }, {
                        title: 'وضعیت',
                        rowCellProps: {
                            width: 50
                        },
                        renderCell: row => (
                            <div>
                                <Chip label="در حال تایید" color="warning" size="small" />
                                <Chip label="در حال ارسال" color="info" size="small" />
                                <Chip label="ارسال شده" color="success" size="small" />
                            </div>
                        ),
                        priority: 300
                    },
                    ...flatten((inject('People', 'PeopleList.definitions') || []))
                ], o => o.priority)}
                renderSheetTitle={row => <Text>{row.name}</Text>}
                renderListItemChildren={(row) => (
                    <ListItemButton onClick={() => navigateTask(taskDetails(row))}>
                        {/* <ListItemAvatar>
                            <Avatar src={row.avatar} />
                        </ListItemAvatar> */}
                        <ListItemText
                            primary={<strong>{row.name}</strong>}
                        // secondary={row.roles && row.roles.length > 0 ? <Text>{row.roles.map(r => r.name).join(', ')}</Text> : '-'}
                        />
                    </ListItemButton>
                )}

                tableRowProps={row => ({
                    hover: true,
                    striped: true,
                    onClick: () => navigateTask(taskDetails(row))
                })}

                paginationProps={{
                    count: meta.total,
                    rowsPerPage: searchParams.get('perPage') || 25,
                    page: searchParams.get('page') || 0,
                    onPageChange: (e, page) => setSearchParams({ ...Object.fromEntries([...searchParams]), page }),
                    onRowsPerPageChange: e => setSearchParams({ ...Object.fromEntries([...searchParams]), perPage: e.target.value })
                }}

                actions={[
                    {
                        title: 'ویرایش',
                        icon: <Edit />,
                        onClick: onClickEdit,
                        authorize: ({ hasRole, hasPermission }) => hasRole('owner') || hasPermission('person:edit')
                    },
                    {
                        title: 'حذف',
                        icon: <DeleteOutline />,
                        onClick: person => onClickDelete(person, false),
                        authorize: ({ hasRole, hasPermission }) => hasRole('owner') || hasPermission('person:delete')
                    }
                ]}
            />
        </>
    )
}

export default DataTable